body {
  margin: 0;
  padding: 0;
}

.main__page {
  margin: 0;
  padding: 0;
}

.hero__image {
  background-image: url("../images/cover.jpg");
  background-position: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero__image .hero__title {
  font-family: "Roboto Slab", serif;
  color: white;
  font-size: 42px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
}

.hero__image .hero__subtitle {
  font-family: "Roboto", sans-serif;
  color: white;
  font-size: 16px;
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 0;
}
